import { Link } from 'gatsby';
import React from 'react';

import content from '../../site/content/components/footer.json';

// const Icon = ({ brand: FaIcon }) => <FaIcon className="text-6xl" />;

interface Props {
  locale: string;
}

const Footer = ({ locale }: Props) => (
  <div
    className={`py-8 bg-${content[locale].background_color} text-${content[locale].text_color}`}
  >
    <div className="container">
      <div className="md:flex md:pb-16">
        <div id="logo" className="h-36">
          <img src={content[locale].logo} className="h-full mx-auto" />
        </div>
        <div
          id="categories"
          className="text-center md:flex md:flex-1 md:py-0 md:pl-8 md:justify-end"
        >
          {content[locale].categories.map(category => (
            <div id="category" className="py-4 md:py-0 md:px-8">
              <h3
                className={`text-2xl text-${content[locale].category_color} p-2`}
              >
                {category.title}
              </h3>
              {category.items.map(item => (
                <div className="p-1">
                  <a
                    href={item.link}
                    target={item.external ? '_blank' : ''}
                    rel={item.external ? 'noopener noreferrer' : ''}
                  >
                    {item.label}
                  </a>
                </div>
              ))}
            </div>
          ))}
        </div>
      </div>
      <div id="subpart" className="md:flex md:flex-row-reverse md:justify-between md:items-center">
        <div id="socials" className="flex justify-center">
          {content[locale].socials.map(social => (
            <a
              href={social.link}
              className="h-14 w-14 p-2"
              target="_blank"
              rel="noopener noreferrer"
            >
              <img
                src={social.icon}
                alt={social.id}
                className={`icon-${social.icon_color}`}
              />
            </a>
          ))}
        </div>
        <p id="copyright" className="text-xs text-center">
          {content[locale].copyright}
        </p>
      </div>
    </div>
  </div>
);

export default Footer;
